.dot{
    position: fixed;
    z-index:200;
    -webkit-animation: mymove 5s infinite; /* Safari 4.0 - 8.0 */
    animation: mymove 5s infinite;
}
.loader{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
}

@-webkit-keyframes mymove {
    0%   {-webkit-transform:scale(0.15);transform:scale(0.15)}
    25%  {-webkit-transform:scale(0.25);transform:scale(0.25)}
    50%  {-webkit-transform:scale(0.35);transform:scale(0.35)}
    75%  {-webkit-transform:scale(0.5);transform:scale(0.5)}
    100% {-webkit-transform:scale(0.25);transform:scale(0.25)}
}

/* Standard syntax */
@keyframes mymove {
    0%   {-webkit-transform:scale(0.15);transform:scale(0.15)}
    25%  {-webkit-transform:scale(0.25);transform:scale(0.25)}
    50%  {-webkit-transform:scale(0.35);transform:scale(0.35)}
    75%  {-webkit-transform:scale(0.5);transform:scale(0.5)}
    100% {-webkit-transform:scale(0.25);transform:scale(0.25)}
}


